import React from "react"
import {
  Box,
  BreadcrumbItem,
  BreadcrumbLink,
  Breadcrumb,
  Text,
} from "@chakra-ui/react"
import { ChevronRightIcon } from "@chakra-ui/icons"
import theme from "../../@chakra-ui/gatsby-plugin/theme"

const Breadcrumbs = ({ breadcrumb }) => {
  const maxNumberOfBreadcrumbsToShow = 4
  const humanizeCrumbLabel = crumbLabel => {
    return crumbLabel.replaceAll("-", " ")
  }

  const computeCrumbProps = crumbIndex => {
    let props = {
      noOfLines: 1,
    }

    if (crumbIndex > 0) {
      props = { ...props, fontWeight: "bold" }
    }

    if (crumbIndex === breadcrumb.crumbs.length - 1) {
      props = {
        ...props,
        color: theme.colors.rlBlack,
      }
    } else {
      props = {
        ...props,
        color: theme.colors.rlGreyDarker,
      }
    }

    return props
  }

  const generateBreadcrumbs = () => {
    const crumbs = breadcrumb?.crumbs

    let uiBreadcrumbs = crumbs?.map((crumb, index) => (
      <BreadcrumbItem key={index}>
        <BreadcrumbLink href={crumb.pathname}>
          <Text casing="capitalize" {...computeCrumbProps(index)}>
            {humanizeCrumbLabel(crumb.crumbLabel)}
          </Text>
        </BreadcrumbLink>
      </BreadcrumbItem>
    ))

    const elipsisBreadcrumb = (
      <BreadcrumbItem key="elipsisBreadcrumb">
        <Text color={theme.colors.rlGreyDarker}>...</Text>
      </BreadcrumbItem>
    )

    if (uiBreadcrumbs.length > maxNumberOfBreadcrumbsToShow) {
      const elementsToRemove =
        uiBreadcrumbs.length - maxNumberOfBreadcrumbsToShow + 1

      //remove breadcrumbs so that they fit in the UI and replace with an alipsis breadcrumb
      uiBreadcrumbs.splice(2, elementsToRemove)
      uiBreadcrumbs.splice(2, 0, elipsisBreadcrumb)
    }

    return uiBreadcrumbs
  }

  return (
    <Box hideBelow="md">
      <Breadcrumb
        separator={<ChevronRightIcon color={theme.colors.rlGreyDarker} />}
      >
        {generateBreadcrumbs()}
      </Breadcrumb>
    </Box>
  )
}
export default Breadcrumbs
