import { Box, Grid, GridItem, Heading } from '@chakra-ui/react';
import Menu from '../components/menu/menu';
import renderDocumentationPageAst from '../utils/render-documentation-page-ast';
import Toc from '../components/toc/toc';
import Feedback from '../components/feedback/feedback';
import * as React from 'react';
import { useMemo } from 'react';
import { getTocData } from '../utils/get-toc-data';
import Breadcrumb from '../components/breadcrumb/breadcrumb';

const DocumentationPage = ({
  data,
  location,
  urlPrefix,
  breadcrumb,
  tocForCurrentSection,
}) => {
  const tocData = useMemo(() => {
    if (data?.toc?.htmlAst == null) {
      return tocForCurrentSection.children;
    }

    try {
      return getTocData(data?.toc?.htmlAst);
    } catch (e) {
      console.error(`Table of contents failed for page ${location?.pathname}`);
    }
  }, [data]);

  const page = data.pageContent;

  return (
    <Grid
      py={20}
      templateColumns={{
        base: '1fr',
        lg: '30% 70%',
        xl: '1fr 42rem 1fr',
      }}
      gap={10}
    >
      <GridItem
        hideBelow="lg"
        maxW={'20rem'}
        pl="1em"
        position="sticky"
        top={115}
        maxH="70vh"
        overflow="auto"
      >
        <Menu
          content={tocData}
          rootTitle={data.toc}
          location={location}
          urlPrefix={urlPrefix}
          isAutogenerated={data?.toc?.htmlAst == null}
        />
      </GridItem>

      <GridItem
        px="1em"
        maxW={{ base: '100vW', sm: '100vW', md: '100vW', lg: '42rem' }}
      >
        <Box mb="1.5em">
          <Breadcrumb breadcrumb={breadcrumb} />
        </Box>
        <article className="DocSearch-content">
          {renderDocumentationPageAst(page.htmlAst, urlPrefix)}
        </article>
      </GridItem>

      <GridItem
        maxW={'20rem'}
        display={{ base: 'none', md: 'none', lg: 'none', xl: 'block' }}
      >
        <Box position="sticky" top={115} pr="1em">
          <Heading fontSize={20} fontWeight={700} pb={2.5}>
            IN THIS PAGE
          </Heading>
          <Box maxH="60vh" overflow="auto">
            <Toc data={data.pageContent} location={location} />
          </Box>
          <Feedback location={location} url />
        </Box>
      </GridItem>
    </Grid>
  );
};

export default DocumentationPage;
