import React, { useEffect, useState } from 'react';
import {
  Box,
  AccordionItem,
  AccordionPanel,
  AccordionButton,
  Accordion,
  AccordionIcon,
} from '@chakra-ui/react';
import { navigate } from 'gatsby';

const MenuItem = ({ items, menuPathPrefix, level, location }) => {
  const [expandedIndex, setExpandedIndex] = useState([]);

  const setMenuData = menuUrlPath => {
    if (menuUrlPath?.startsWith('#') || !menuUrlPath) {
      return '#';
    } else {
      return `/${menuPathPrefix}/${menuUrlPath}`.replace(/\/+/g, '/');
    }
  };

  const depthFirstSearch = (v, currentLocation, found) => {
    v.visited = true;

    if (setMenuData(v.href) === currentLocation) {
      found.value = true;
    }

    for (let child of v.children) {
      if (child.visited == null || child.visited === false) {
        depthFirstSearch(child, currentLocation, found);
      }
    }
  };

  /**
   * searches if the children contain the url of the current page
   * @param node
   * @returns {{value: boolean}}
   */
  const searchCurrentPath = node => {
    let copy = JSON.parse(JSON.stringify(node));
    let found = { value: false };
    const currentLocation = location.pathname.replace(/\/$/, '');
    depthFirstSearch(copy, currentLocation, found);

    return found;
  };

  useEffect(() => {
    for (let index = 0; index < items.length; index++) {
      const found = searchCurrentPath(items[index]);
      if (found.value) {
        setExpandedIndex([index]);
        break;
      }
    }
  }, []);

  const navigateToPage = item => {
    if (item?.children?.length === 0 && item.href) {
      navigate(setMenuData(item.href));
    }
  };

  const handleAccordionItemChange = index => {
    setExpandedIndex([index]);
  };

  return (
    <Accordion
      index={expandedIndex}
      onChange={handleAccordionItemChange}
      allowToggle
    >
      {items?.map((item, index) => (
        <AccordionItem
          className={expandedIndex.includes(index) && 'active'}
          key={item.textElement}
          border="none"
          paddingBottom="4px"
        >
          <AccordionButton
            paddingLeft="0"
            _hover={{
              background: 'transparent',
            }}
            py="0.1em"
            onClick={() => navigateToPage(item)}
          >
            {item.children.length > 0 ? <AccordionIcon /> : <Box w="1.3em" />}
            <Box
              as="span"
              flex="1"
              pl="0.5em"
              textAlign="left"
              fontWeight={level === 0 ? 600 : null}
              fontSize={20}
              lineHeight={1.2}
              background={
                expandedIndex.includes(index) && item.children.length === 0
                  ? 'rlGrey20'
                  : 'transparent'
              }
              borderRadius={5}
              _hover={{
                background: item.href && 'rlGrey20',
              }}
            >
              {item.textElement}
            </Box>
          </AccordionButton>
          <AccordionPanel py={0}>
            <MenuItem
              menuPathPrefix={menuPathPrefix}
              items={item.children}
              level={level + 1}
              location={location}
            />
          </AccordionPanel>
        </AccordionItem>
      ))}
    </Accordion>
  );
};

export default MenuItem;
